import { AnimatePresence, motion } from 'framer-motion';
import React from 'react';
import { transitions } from '../../..';
import { clsx } from '../../../libs/clsx';
import { Icon, MonochromeIcons } from '../../icons';
import { Flex, Inset } from '../../layout';
import styles from './alert.less';

interface AlertProps {
  label?: string;
  type?: 'important' | 'success' | 'error' | 'warning' | 'neutral';
  icon?: boolean;
  dismissable?: boolean;
  children?: any;
}

export const Alert: React.FC<AlertProps> = (props) => {
  const { label, type = 'important', icon = true, dismissable = false, children } = props;

  const [dismissed, setDismissed] = React.useState(false);
  const getScaleProps = transitions.useScale();

  let iconType;
  switch (type) {
    case 'success':
      iconType = MonochromeIcons.SuccessFilled;
      break;

    case 'warning':
      iconType = MonochromeIcons.Warning;
      break;

    case 'error':
      iconType = MonochromeIcons.Exclaim;
      break;

    case 'neutral':
      iconType = MonochromeIcons.Info;
      break;

    case 'important':
    default:
      iconType = MonochromeIcons.Info;
      break;
  }

  return (
    <AnimatePresence initial={false}>
      {!dismissed && (
        <motion.div className={clsx([styles.Alert, styles[type as any]])} {...getScaleProps(0.85)}>
          <Flex.Row vertical="flex-start" style={{ width: '100%' }}>
            {icon && (
              <Inset top={2}>
                <Icon size={20} type={iconType} className={styles.icon} />
              </Inset>
            )}

            <Flex.Item grow={1} role="status" className={styles.message}>
              {label && <div className={styles.label}>{label}</div>}
              {children}
            </Flex.Item>

            {dismissable && (
              <Inset top={2}>
                <button className={styles.dismissable} type="button" onClick={() => setDismissed(true)}>
                  <Icon size={20} type={MonochromeIcons.Remove} />
                </button>
              </Inset>
            )}
          </Flex.Row>
        </motion.div>
      )}
    </AnimatePresence>
  );
};
